
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

$size: 200px;

.activity {
  width: $size;
  height: $size;
  position: relative;
  background-color: $white-ter;

  &.hovered > img {
    filter: brightness(80%);
  }
}

a {
  color: white;

  &:hover {
    filter: brightness(80%);
  }
}

.activity-top,
.activity-bottom {
  position: absolute;
  width: 100%;
  left: 0;
  background-color: rgba($color: #000, $alpha: 0.55);
  color: white;
}

.activity-bottom {
  bottom: 0;
}

.activity-top {
  top: 0;
}

.activity-center {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;

  button {
    pointer-events: auto;
  }
}
