
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

tr.masked {
  background: repeating-linear-gradient(
    45deg,
    rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 10px,
    rgba(0, 0, 0, 0) 10px,
    rgba(0, 0, 0, 0) 20px
  );
  background-attachment: scroll;
}

td {
  white-space: nowrap;
}

td:nth-child(4) {
  width: 100%;
  white-space: normal;
  font-style: italic;
}
input[type='radio'] {
  margin-right: 5px;
}
